.preloader {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 9999;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  background-color: #000;
}
.preloader .header__title {
  z-index: 12;
  margin: 0;
  padding: 0;
  width: auto;
  text-align: center;
}
.preloader--disappear {
  animation-name: hide;
  animation: 1.4s linear infinite;
}
.preloader__content {
  position: relative;
}
.preloader__widget-wrap {
  z-index: 1;
  position: absolute;
  left: calc(50% - 50px);
  top: calc(50% - 50px);
  width: 100px;
  height: 100px;
}
.preloader__widget-first, .preloader__widget-second, .preloader__widget-third {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  animation: 1s linear infinite;
}
.preloader__widget-first {
  border-bottom: 2px solid #c4c4c4;
  animation-name: first;
}
.preloader__widget-second {
  border-right: 2px solid #bedef3;
  animation-name: second;
}
.preloader__widget-third {
  border-top: 2px solid #d5303a;
  animation-name: third;
}

@keyframes first {
  0% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
  }
  100% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
  }
}
@keyframes second {
  0% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
  }
  100% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
  }
}
@keyframes third {
  0% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
  }
  100% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
  }
}
@keyframes hide {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
