.video {
  width: 100%;
}
.video__hidden {
  visibility: hidden;
}
.video__wrap {
  position: relative;
  max-width: 100%;
  margin-bottom: 80px;
  padding-bottom: 40%;
  padding-top: 0;
  height: 0;
  overflow: hidden;
}
.video__wrap--main {
  padding-bottom: 40%;
}
.video__wrap--01 {
  padding-bottom: 37.5%;
}
.video__wrap--02 {
  padding-bottom: 40.625%;
}
.video__wrap--03 {
  padding-bottom: 55%;
}
.video__wrap--04 {
  padding-bottom: 36.875%;
}
.video__wrap--05 {
  padding-bottom: 40.625%;
}
.video__wrap--06 {
  padding-bottom: 41.25%;
}
.video__wrap--07 {
  padding-bottom: 41.25%;
}
.video__wrap--08 {
  padding-bottom: 40.625%;
}
.video__wrap--chrom {
  padding-bottom: 55%;
}
.video iframe, .video object, .video embed, .video video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}
.video__cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.video__cover::before {
  content: "";
  position: absolute;
  z-index: 40;
  top: calc(50% - 25px);
  left: calc(50% - 25px);
  width: 50px;
  height: 50px;
  margin: 0;
  background: transparent;
  border-top: 4px solid #fbfbfb;
  border-right: 4px solid transparent;
  border-radius: 50%;
  animation: 1s spin linear infinite;
}
.video__cover img {
  width: 100%;
  height: auto;
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.yt-video {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  background-color: #000;
}

.yt-video__embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

.yt-video__media {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

.yt-video__button {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 68px;
  height: 48px;
  border: none;
  background-color: transparent;
  transform: translate(-50%, -50%);
  cursor: pointer;
  outline: none;
}

.yt-video__button-shape {
  fill: #212121;
  fill-opacity: 0.8;
}

.yt-video:hover .yt-video__button-shape,
.yt-video-button:focus .yt-video__button-shape {
  fill: #f00;
  fill-opacity: 1;
}

.yt-video__button-icon {
  fill: #fff;
}

.yt-video--enabled .yt-video__button {
  display: block;
}